import React, { useMemo, useState } from "react";
import styles from "./ProductTitle.module.scss";

const ProductTitleComponent = ({ setVariant, product, variationOptions }) => {
  const { Model, ProductTitle } = product;
  const [showDropdown, setShowDropdown] = useState(false);

  return useMemo(() => {
    const toggleVariant = () => {
      setVariant(null);
      setShowDropdown(!showDropdown);
    };

    const getModelText = () => {
      const words = Model.split('/')
      if (words.length >= 2) {
        return words.join(' / ')
      } else {
        return words.join('')
      }
    }
    return (
      <div className={variationOptions.length && styles.variantDropdown}>
        <div onClick={toggleVariant}>
          <p className={styles.productModel}>
            {getModelText()} {variationOptions.length > 0 && <i className={showDropdown ? "fas fa-caret-up" : "fas fa-caret-down"}></i>}
          </p>
          <p>{ProductTitle}</p>
        </div>
        {showDropdown &&
          variationOptions.map((variantProduct) => (
            <p key={variantProduct.Model} className={styles.variantTitle} onClick={() => setVariant(variantProduct)}>
              <a href={variantProduct.Link} target="_blank" rel="noreferrer" className={variantProduct.Model}>{variantProduct.Model} - {variantProduct.ProductTitle}</a>
            </p>
          ))}
      </div>
    );
  }, [Model, variationOptions, ProductTitle, showDropdown, setVariant]);
};

export { ProductTitleComponent };
