import React, { useRef, useEffect } from "react";
import { appConfig } from "../config";

const VietnamComponent = ({ getIframe }) => {
  const iframe = useRef(null);

  useEffect(() => {
    getIframe(iframe);
  }, [getIframe]);

  return (
    <>
      <iframe
        id="vista"
        ref={iframe}
        style={{ height: "100%", width: "100%", overflow: "hidden", border: "none" }}
        allowFullScreen={false}
        title="Vietnam"
        scrolling="no"
        src={appConfig.vistaUrl}
      ></iframe>
    </>
  );
};

const Vietnam = React.memo(VietnamComponent);

export { Vietnam };
